/* Fonts */

@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400&display=swap');

aside.col-1
{
    display: flex;
    align-items: stretch;
    border-left: 1px solid #bfbfbf;
}

h2
{
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 2vh;
    margin-left: 50px;
}

#cartempty
{
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 1.5vh;
    color: #bfbfbf;
    margin-left: 50px;
}

.cart
{
    display: flex;
    flex-flow: row nowrap;
    margin-left: 10px;
    width: 100%;
}

.col-2.itemname
{
    display: flex;
    flex-flow: row nowrap;
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 1.6vh;
}

.col-2.cartbuttons
{
    display: inline-flex;
    gap: 5px;
    margin-left: 40px;
    
}

.col-2.qty-price
{
    font-family: "Roboto Condensed", "sans-serif";
    white-space: pre-wrap;
}

.add, .remove
{
    display: inline-flex;
    justify-content: center;
    align-content: center;
    background-color: #bfbfbf;
    height: 20px;
    width: 25px;
    border: none;
    color: white;
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 15px;
    text-decoration: none;
    cursor: pointer;
}

.pricelist
{
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 1.7vh;
    text-align: left;
    line-height: 5%;
    border-top: 1px solid #bfbfbf;
    border-bottom: 1px solid #bfbfbf;
    width: 80%;
    margin: 40px;
}

.col-1.text-right
{
    text-align: right;
}

#checkout
{
    display: inline-block;
    width: 100px;
    background-color: slategrey;
    border: none;
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 15px;
    color: white;
    padding: 10px;
    text-decoration: none;
}

.orderform
{
    margin: auto;
}

form
{
    font-family: "Roboto Condensed", "sans-serif";
    text-align: left;
    margin: 20px auto auto auto;
}

input
{
    margin-bottom: 10px;
}

input[type="submit"]
{
    display: block;
    width: 100px;
    background-color: slategrey;
    border: none;
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 15px;
    color: white;
    padding: 10px;
    text-decoration: none;
    margin: 10px auto 30px auto;
}

.ordersummary 
{
    font-family: "Roboto Condensed", "sans-serif";
    margin: auto 40px auto 40px;
}

#summaryheader 
{
    font-family: "Roboto Condensed", "sans-serif";
    text-align: center;
    margin-top: 40px;
}

#customermessage
{
    font-family: "Roboto Condensed", "sans-serif";
    color: slategrey;
    margin-top: 20px;
}

#itemsummary, #pricesummary
{
    margin: 20px auto auto 30px;
    border-spacing: 20px;
}

th 
{
    text-align: left;
}

#item-qty, #item-price 
{
    text-align: center;
}

h4 
{
    margin: 20px auto auto 50px;
}

.deliverydetails
{
    margin: 30px auto auto 50px;
}

.customerdetails td
{
    padding-left: 40px;
}

#closebutton
{
    display: block;
    width: 100px;
    background-color: slategrey;
    border: none;
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 15px;
    color: white;
    padding: 10px;
    text-decoration: none;
    margin: 30px auto 30px 50px;
}