/* Fonts */

@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo:wght@100&family=Roboto+Condensed:wght@300;400;700&display=swap');

/* Header.js CSS */

.app-header
{
  background-color: slategrey;
  font-family: "Oswald", "sans-serif";
  font-size: 4em;
  color: #FFFFFF;
  text-align: center;
  padding: 80px auto 80px auto;
  margin-bottom: 0;
}

h1 
{
  margin: 0;
  padding: 40px;
}

.app-navbar
{
  display: flex;
  justify-content: right;
  width: 100%;
  height: 50px;
  background-color: #bfbfbf;
  margin-top: 0;
}

.row
{
  display: flex;
  justify-content: space-between;
  margin: 40px;
}

.col-1
{
  flex: 1;
  flex-direction: column;
}

.col-2
{
  flex: 2;
}

#cart
{
  font-family: "Roboto Condensed", "sans-serif";
  font-size: 18px;
  color: #FFFFFF;
  text-decoration: none;
  line-height: 50px;
  border: none;
  width: 150px;
}