/* Fonts */

@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400&display=swap');

.productcomponent
{
   margin: 40px;
}

#name
{
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 1.7vh;
}

#price 
{
    font-family: "Roboto Condensed", "sans-serif";
    font-weight: bold;
    font-size: 20px;
}

.productbuttons
{
    display: flex;
    flex-flow: column wrap;
    justify-content: flex-start;
    margin-top: 30px;
    gap: 10px;
}

#info
{
    font-family: "Roboto Condensed", "sans-serif";
    font-weight: 300;
}

#infobutton
{
    display: inline-block;
    width: 100px;
    background-color: #bfbfbf;
    border: none;
    color: white;
    padding: 10px;
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 15px;
    text-decoration: none;
}

#addtocartbutton
{
    display: inline-block;
    width: 100px;
    background-color: slategrey;
    border: none;
    font-family: "Roboto Condensed", "sans-serif";
    font-size: 15px;
    color: white;
    padding: 10px;
    text-decoration: none;
}

p
{
    font-family: "Roboto Condensed", "sans-serif";
}